@import "../../../variables";


.heading{
    font-weight: 600;
    display: flex;
    align-items: baseline;

    &__dote{
        display: inline-block;
        width: 0px;
        height: 0px;
        background-color: $mainControl;
        border-radius: 50%;
        margin: 0;

        &._1{
            width: 6px;
            height: 6px;
            margin: 0 3px;
        }
        &._2{
            width: 4px;
            height: 4px;
            margin: 0 2px;
        }
    }
}

